@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
html {
  font-size: 1rem;
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Helvetica Neue', 'Calibri Light', Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@media screen and (max-width: 768px) {
  #text-column {
    order: 2; } }

.font-questrial {
  font-family: 'Questrial', sans-serif; }

.container {
  max-width: 1180px;
  padding: 0 2em;
  margin: 0 auto; }

.page-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 500px; }
  .page-wrapper .page-header {
    position: relative;
    padding-top: 2em; }
    .page-wrapper .page-header .brand {
      display: inline-block; }
      .page-wrapper .page-header .brand .lead-text {
        font-size: 2.5em;
        line-height: 1.5em;
        letter-spacing: 0.01em;
        margin: 0; }
      .page-wrapper .page-header .brand .sub-text {
        margin-top: -1em;
        margin-bottom: 0;
        line-height: 1.5em;
        letter-spacing: 0.22em; }
  .page-wrapper .page-content {
    position: relative;
    top: 12%; }
    .page-wrapper .page-content .banner .text-wrapper {
      margin-top: 2em; }
      .page-wrapper .page-content .banner .text-wrapper .title {
        font-size: 3.6em; }
      .page-wrapper .page-content .banner .text-wrapper .text-body {
        font-size: 1.5em;
        line-height: 1.8em; }
    .page-wrapper .page-content .banner .image-wrapper {
      padding: 1em; }
      .page-wrapper .page-content .banner .image-wrapper img {
        width: 100%; }
    @media screen and (min-width: 1024px) {
      .page-wrapper .page-content .banner::after {
        position: relative;
        content: " ";
        display: block;
        bottom: -100px;
        left: -15px;
        width: 100%;
        border-style: solid;
        border-width: 100px 100vw 0 0;
        border-color: white transparent transparent transparent; } }
    .page-wrapper .page-content .features {
      padding-top: 100px;
      background-color: #3D8CBF;
      width: 100%; }
      .page-wrapper .page-content .features .feature {
        height: 300px; }
        .page-wrapper .page-content .features .feature h1, .page-wrapper .page-content .features .feature h2, .page-wrapper .page-content .features .feature h3, .page-wrapper .page-content .features .feature h4, .page-wrapper .page-content .features .feature span {
          color: white !important; }
      @media screen and (min-width: 1024px) {
        .page-wrapper .page-content .features::after {
          position: relative;
          content: " ";
          display: block;
          bottom: -100px;
          left: -15px;
          width: 100%;
          border-style: solid;
          border-width: 100px 100vw 0 0;
          border-color: #3D8CBF transparent transparent transparent; } }
  .page-wrapper .page-footer {
    height: 500px;
    padding-top: 200px; }
    .page-wrapper .page-footer .container {
      height: 100%; }
    .page-wrapper .page-footer .footer-links {
      height: 100%; }
      .page-wrapper .page-footer .footer-links .links {
        list-style: none;
        padding: 0; }
        .page-wrapper .page-footer .footer-links .links li a {
          color: rgba(0, 0, 0, 0.65); }
          .page-wrapper .page-footer .footer-links .links li a:hover {
            color: rgba(0, 0, 0, 0.85); }
    .page-wrapper .page-footer .footer-copy {
      padding-top: 10px;
      border-top: 1px solid rgba(192, 192, 192, 0.5); }

